<template>
    <div class="list-tours">
        <div ref="SubPagesLoader">
            <nested-pages-loader :can-show="showLoader"></nested-pages-loader>
        </div>

        <router-view v-slot="{ Component }">
            <transition @enter="onEnter" @leave="onLeave" :css="false" mode="out-in">
                <component :is="Component" />
            </transition>
        </router-view>
    </div>
</template>


<script setup>

import NestedPagesLoader from "@/components/app-ui/NestedPagesLoader";
import { ref } from "vue";
import { transitionEnter, transitionLeave } from "@/utils/nestedPagesTransition";

const SubPagesLoader = ref(null);
const showLoader = ref(false);

function onEnter() {
    transitionEnter(SubPagesLoader.value, () => {
        showLoader.value = false;
    });
}

function onLeave(el, done) {
    showLoader.value = true;
    transitionLeave(SubPagesLoader.value, done);
}

</script>