import { gsap } from "gsap";

const titleDistanceY = -30;

function transitionEnter(elem , onFinish){
    const timeline = gsap.timeline({
        repeat: 0 ,
        onComplete : onFinish
    });

    const delay = .2;
    const{parent , cubes , title , slideOverlay} = _getAnimChilds(elem);

    timeline.set(cubes , {scale:1});
    timeline.set(parent , {autoAlpha : 1});
    timeline.set(slideOverlay , {x : "-100%"});

    timeline.to(title, {
        y: titleDistanceY,
        autoAlpha : 0,
        duration: .5,
        ease : "back.in(1.7)",
    } , "+="+delay);

    timeline.to(parent, {
        autoAlpha: 0,
        duration: .8,
    } );

}

function transitionLeave(elem , done) {
    
    const delay = .3; // useful for showing active links transition
    const{parent , cubes , title , slideOverlay} = _getAnimChilds(elem);
    
    const timeline = gsap.timeline({
        repeat: 0
    });

    timeline.set(cubes , { scale:0});
    timeline.set(parent , {autoAlpha : 0});
    timeline.set(slideOverlay , {x : "100%"});

    timeline.to(slideOverlay, {
        x: 0,
        duration: 1.4,
        ease: "expo.inOut",
    });

    timeline.to(parent, {
        autoAlpha: 1,
        duration: .3,
        onComplete: () => {
            _animateCubesIn(cubes, done);
        }
    } , "-="+delay);


    timeline.to(title, {
        y: 'inherit',
        autoAlpha : 1,
        duration: .5,
        ease : "back.out(1.7)",
    } , "-=.1");
}


function _animateCubesIn(cubes , onFinish){
    gsap.to(cubes, {
        scale: 1,
        duration: .4,
        ease: "back.out(4)",
        onComplete : onFinish
    });
}

function _getAnimChilds(elem) {
    const parent = elem.querySelector(".p-loader");
    const cubes = parent.querySelector(".boxes");
    const title = parent.querySelector(".loading-title");
    const slideOverlay = elem.querySelector(".slide-overlay");
    return { parent , cubes , title , slideOverlay };
}




export {transitionEnter , transitionLeave}